und: $light-background;\n  height: 35px;\n}\n\n.explorer-ul {\n  &,\n  ul {\n    padding: 0;\n    margin: 0;\n    list-style: none;\n    font-size: 15px;\n    line-height: 35px;\n  }\n  span {\n    display: block;\n  }\n  .file,\n  .folder {\n    position: relative;\n    display: block;\n    cursor: pointer;\n    text-decoration: none;\n    color: $light-color;\n    word-wrap: normal;\n    white-space: nowrap;\n    .item-content {\n      box-shadow: none;\n      outline: none;\n    }\n    &:hover {\n      background: $light-background-seconday;\n    }\n    .text {\n      position: relative;\n      padding-left: 50px;\n      margin: 0 8px;\n    }\n    .icon {\n      display: block;\n      position: absolute;\n      top: 5px;\n      left: 18px;\n      height: 20px;\n      width: 20px;\n      color: $light-color;\n    }\n    .icon-caret-down {\n      display: block;\n      position: absolute;\n      z-index: 1000;\n      top: 10px;\n      left: 0;\n      height: 16px;\n      width: 16px;\n    }\n\n    outline: 1px dotted transparent;\n    &:focus {\n      outline: 1px dotted darken($light-background-seconday, 10);\n    }\n\n    &.disabled {\n      opacity: 0.4;\n      cursor: not-allowed;\n      &:hover {\n        background: transparent;\n      }\n      &:focus {\n        outline: 1px dotted transparent;\n      }\n    }\n    &.selected {\n      background: $light-background-seconday;\n      font-weight: bold;\n      color: #6db33f;\n    }\n  }\n\n  .folder-hide {\n    > ul {\n      display: none;\n    }\n    > .folder > span > .text > .icon-caret-down {\n      transform: rotate(-90deg);\n    }\n  }\n\n  $range: 12px;\n\n  .level-0 {\n    .text {\n      margin-left: 0 * $range + 6px;\n    }\n  }\n  .level-1 {\n    .text {\n      margin-left: 1 * $range + 6px;\n    }\n  }\n  .level-2 {\n    .text {\n      margin-left: 2 * $range + 6px;\n    }\n  }\n  .level-3 {\n    .text {\n      margin-left: 3 * $range + 6px;\n    }\n  }\n  .level-4 {\n    .text {\n      margin-left: 